import {
  Button,
  Box,
  Header as Head,
  Menu,
  ResponsiveContext,
  Text,
} from "grommet";
import { Home, Apps, Copy, Moon, Sun } from "grommet-icons";

import Docs from "./features/docs/Docs";
import Main from "./Main";

const Header = ({ darkMode, setMode, link }) => {
  const darkModeBtn = (
    <Button
      onClick={() => setMode((darkMode = !darkMode))}
      icon={darkMode ? <Sun color="background" /> : <Moon color="background" />}
    />
  );

  const burgerMenuItems = [
    {
      label: "Docs",
      icon: <Copy />,
      reverse: true,
      onClick: () => link(<Docs />),
    },
  ];

  return (
    <Head background="gradient" pad="medium" height="xsmall" round="medium">
      <ResponsiveContext.Consumer>
        {(size) =>
          size !== "small" ? (
            <>
              <Button
                margin={{ left: "medium" }}
                plain
                onClick={() => link(<Main />)}
              >
                <Home color="background" />
                <Text
                  color="background"
                  margin={{ left: "xsmall" }}
                  size="large"
                >
                  conveyance me
                </Text>
              </Button>
              <Box justify="start">
                <Button
                  icon={<Copy color="background" />}
                  reverse
                  plain
                  onClick={() => link(<Docs />)}
                />
              </Box>
              <Box justify="end">{darkModeBtn}</Box>
            </>
          ) : (
            <>
              <Button
                as="span"
                icon={<Home color="background" />}
                onClick={() => link(<Main />)}
              />
              <Menu
                a11yTitle="Navigation Menu"
                dropProps={{ align: { top: "bottom", right: "right" } }}
                icon={<Apps color="background" />}
                items={burgerMenuItems}
                dropBackground="#c3c3c3"
                size="medium"
              />
            </>
          )
        }
      </ResponsiveContext.Consumer>
    </Head>
  );
};

export default Header;
