import { createSlice } from "@reduxjs/toolkit";
import firebase from "../../firebase";
const store = firebase.storage();

const slice = createSlice({
  name: "docs",
  initialState: {
    error: null,
  },
  reducers: {
    uploadOk: (state, { payload }) => {
      console.log(state);
      console.log(payload);
    },
    uploadProgress: (state, { payload }) => {
      const { _filename, _progress } = payload;
      state[_filename] = _progress;
    },
    uploadFail: (state, { payload }) => {
      state.error = payload.error;
    },
  },
});
export default slice.reducer;

const { uploadOk, uploadFail, uploadProgress } = slice.actions;
export const uploadFile = (file) => async (dispatch) => {
  try {
    let uploader = store.ref(file.name).put(file);
    uploader.on("state_changed", ({ ref, bytesTransferred, totalBytes }) => {
      let _progress = Math.round((bytesTransferred / totalBytes) * 100);
      let _filename = ref.name;
      dispatch(uploadProgress({ _filename, _progress }));
    });
    let _filename = file.name;
    dispatch(uploadOk(_filename));
  } catch (error) {
    dispatch(uploadFail(error));
  }
};
