import {
  Box,
  Text,
  Accordion as GrommetAccordion,
  AccordionPanel,
  Anchor,
  DataTable,
} from "grommet";
import { FormAttachment } from "grommet-icons";

import List from "./List";

const Accordion = ({ panels, columns }) => {
  const panelText = (panel) => (
    <Box gap="medium">
      <Text>{panel.body}</Text>
    </Box>
  );

  const panelTable = (panel) => (
    <Box align="center" pad="medium">
      <DataTable columns={columns} data={panel.table} resizeable />
    </Box>
  );

  const panelLink = (panel) => (
    <Box pad={{ top: "small" }} direction="row">
      <FormAttachment />
      <Anchor
        href={panel.link.url}
        target="_blank"
        label={panel.link.label}
        margin={{ left: "xxsmall" }}
      />
    </Box>
  );

  return (
    <GrommetAccordion margin={{ vertical: "small" }}>
      {panels.map((panel) => (
        <AccordionPanel
          key={panel.header}
          label={
            <Text
              margin={{ left: "xsmall", vertical: "small" }}
              size="large"
              weight={0}
            >
              {panel.header}
            </Text>
          }
        >
          <Box pad={{ left: "xsmall", bottom: "medium" }}>
            {panelText(panel)}
            {panel.list ? List(panel) : null}
            {panel.table ? panelTable(panel) : null}
            {panel.link ? panelLink(panel) : null}
          </Box>
        </AccordionPanel>
      ))}
    </GrommetAccordion>
  );
};

export default Accordion;
