import { useState, useContext } from "react";
import { Grommet, Box, ResponsiveContext } from "grommet";
import { grommet } from "grommet/themes";
import { deepMerge } from "grommet/utils";
import themeMods from "./themeMods";

import Header from "./Header";
import Main from "./Main";
import Side from "./Side";
import Footer from "./Footer";

const theme = deepMerge(grommet, themeMods);

const App = () => {
  const [darkMode, setDarkMode] = useState(true);
  const [link, linkTo] = useState(<Main />);
  const size = useContext(ResponsiveContext);
  return (
    <Grommet full theme={theme} themeMode={darkMode ? "dark" : "light"}>
      <Box>
        <Header darkMode={darkMode} setMode={setDarkMode} link={linkTo} />
      </Box>

      <Box
        direction="row-responsive"
        justify="center"
        align="center"
        pad="medium"
        gap="small"
      >
        <Box width={size !== "small" ? "100%" : "65%"}>{link}</Box>

        <Box width={size !== "small" ? "100%" : "35%"}>
          <Side />
        </Box>
      </Box>

      <Box>
        <Footer />
      </Box>
    </Grommet>
  );
};

export default App;
